body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sim-header{grid-area:header;text-align:center;background-color:#1F1825;font-size:4em;padding:0.5em;color:white}.sim-sidebar{grid-area:sidebar;text-align:center;background-color:#1E565C;padding-left:0.5em;padding-right:5em;color:white}.sim-content{grid-area:main}.sim-footer{grid-area:footer;text-align:center;background-color:#1F1825;color:white}.sim-wrapper{display:grid;grid-template-areas:'header header header header header header' 'sidebar main main main main main' 'sidebar footer footer footer footer footer'}.num-input{width:100%}.num-input .slider{-webkit-appearance:none;-moz-appearance:none;appearance:none;width:100%;height:25px;background:#d3d3d3;outline:none;opacity:0.7;transition:opacity .2s}.num-input .slider:hover{opacity:1}

.preview-card{padding:10px;margin:10px;background-color:#1E565C;border-radius:10px;width:25%}.preview-card:hover{background-color:#0D7786;box-shadow:4px 4px #1E565C}.preview-card .card-title{color:white;font-size:2em}.preview-card .card-descriptor{font-size:1.5em;color:white}

.home{background-color:#1F1825;color:white;width:100%;height:100%;text-align:center;padding-bottom:2em}.home h1{font-size:4em;padding:0.5em;margin-bottom:0.1em}.home hr{color:grey;width:90%}.home .home-heading{background-color:#1F1825;padding-bottom:2em}.home .card-container{display:grid;grid-template-columns:auto}

